/*
* Provides ability to configure displaying of table columns. As per definition of earch header component,
* a column could be hidden.
*/
import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
	selector: 'table-configuration',
	template: `
<div class="shim"
    [style.display]="showConfigurationView ? 'block':'none'"
    (click)="showConfigurationView = !showConfigurationView"></div>

<a  [attr.tabindex]="0" *ngIf="printTable"
    class="print-table"
    (keyup)="keyup($event)"
    (click)="print($event)">
    <span class="icon fa fa-print" aria-hidden="true"></span>
    <span class="off-screen" [textContent]="print"></span>
</a>
<a  [attr.tabindex]="0"
    class="configure-table"
    (keyup)="keyup($event)"
    (click)="showConfigurationView = !showConfigurationView">
    <span class="icon fa fa-gear" aria-hidden="true"></span>
    <span class="off-screen" [textContent]="action"></span>
</a>
<ng-container *ngIf="configAddon" [ngTemplateOutlet]="configAddon"></ng-container>

<ul role="list" [style.display]="showConfigurationView ? 'block':'none'">
    <p [textContent]="title"></p>
    <li  *ngFor="let header of headers" role="listitem">
        <label for="{{header.key ? header.key+'f':'f'}}">
            <input type="checkbox" #filter
                    [id]="header.key ? header.key+'f':'f'"
                    [checked]="header.filter !== undefined"
                    (keyup)="keyup($event)"
                    (click)="enableFilter(filter, header)" />
            <span>Filter</span>
        </label>
        <label for="{{header.key ? header.key+'c':'c'}}">
            <input type="checkbox" #checkbox
                    [id]="header.key ? header.key+'c':'c'"
                    [value]="header.key"
                    [checked]="header.present"
                    (keyup)="keyup($event)"
                    (click)="reconfigure(checkbox, header)" />
            <span>Show</span>
        </label>
        <span class="title" [textContent]="header.value | uppercase"></span>
    </li>
</ul>
`,
	styles: [`:host{-webkit-box-sizing:border-box;box-sizing:border-box;padding:2px;position:absolute;right:8px;top:18px;z-index:2}:host a{display:block;float:left;padding:0 0 0 10px;cursor:pointer;z-index:5}:host a .icon{color:#00925b}:host a .off-screen{display:block;text-indent:-9999px;width:0;height:0;overflow:hidden}:host .shim{background-color:rgba(255,255,255,.2);width:100vw;height:100vh;position:fixed;top:0;left:0;z-index:2}:host ul{background-color:#fff;border:1px solid #999;border-radius:4px;display:-webkit-box;display:-ms-flexbox;display:flex;list-style:none;max-height:200px;margin:0 2px;min-width:200px;overflow-y:auto;position:absolute;padding:0 0 8px;right:0;-webkit-box-shadow:6px 8px 6px -6px #1b1b1b;box-shadow:6px 8px 6px -6px #1b1b1b;z-index:6}:host ul p{margin:0;padding:1px 5px;background-color:#5f9ea0;color:#fff}:host ul li{white-space:nowrap;text-align:left;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;padding:1px 5px}:host ul li label input{-webkit-box-flex:0;-ms-flex:0 0 30%;flex:0 0 30%;margin-top:4px}:host ul li .title{-webkit-box-flex:0;-ms-flex:0 0 30%;flex:0 0 30%;font-weight:700}`]
})
export class ConfigurationComponent {
    showConfigurationView: boolean;

	@Input("title")
	public title: string;

	@Input("action")
	public action: string;

	@Input("printTable")
	public printTable: string;
	
	@Input("headers")
	public headers: any[];

	@Input("configAddon")
	public configAddon: any;

	@Output('onchange')
	private onchange = new EventEmitter();

	@Output('onprint')
	private onprint = new EventEmitter();

	reconfigure(item, header) {
        header.present = item.checked;
		this.onchange.emit(this.headers);
	}

	enableFilter(item, header) {
        if (header.filter === undefined) {
			header.filter = "";
		} else {
			delete header.filter;
		}
		this.onchange.emit(this.headers);
	}

	print(event) {
		this.onprint.emit(true);
	}

    keyup(event) {
        const code = event.which;
        if (code === 13) {
			event.target.click();
		}
    }
}
