/*
* Provides rendering of a table which is using the given FlexibleTableHeader set in
* order to tabulate the given data. As per definition of earch header component,
* a column could be hidden, sortable, or draggable. Each table row can expand/collapse
* or respond to a click action.
*/
import {
    Component,
	Input,
	Output,
	ViewChild,
	ViewContainerRef,
	OnInit,
	OnChanges,
	EventEmitter,
	ElementRef
} from '@angular/core';

import { DropEvent, DragEvent } from 'drag-enabled';
import { Timeouts } from '../../../../node_modules/@types/selenium-webdriver';
import { Time } from '../../../../node_modules/@angular/common';

export interface FlexibleTableHeader {
	key: string,
	value: string,
	present: boolean,
	width?: string,
	minwidth?: string,
	format?: string,
	filter?: string,
	dragable?: boolean,
	sortable?: boolean,
	class?:string,
	locked?:boolean,
	ascending?: boolean,
	descending?: boolean
}

@Component({
	selector: 'table-view',
	template: `
<table [class]="tableClass"  #flexible>
    <caption *ngIf="caption" [textContent]="caption"></caption>
    <thead>
        <tr>
            <th scope="col" *ngIf="enableIndexing && !printMode" id="indexable" class="indexable"></th>
            <th scope="col" *ngFor="let header of headers"
                [dragEnabled]="dragEnabled.bind(this)"
                [dropEnabled]="dropEnabled.bind(this)"
                [medium]="header"
                (onDragStart)="onDragStart($event)"
                (onDragEnd)="onDragEnd($event)"
                (onDrop)="onDrop($event)"
                [id]="header.key"
                [attr.width]="header.width ? header.width : null" 
                [style.min-width]="header.minwidth ? header.minwidth : ''" 
                [attr.tabindex]="header.sortable ? 0 : -1"
                (keydown)="keydown($event, th)" (click)="sort(header, icon)">
                <span *ngIf="!printMode && header.sortable" class="off-screen"  [textContent]="vocabulary.clickSort"></span>
                <span class="locker icon fa" #locker
                        *ngIf="!printMode && lockable && (headers.length > 1 || header.locked)"
                        tabindex="0"
                        title="lock/unlock this column"
                        (keydown)="keydown($event, locker)" (click)="lock(header, $event)"
                        [class.fa-lock]="header.locked"
                        [class.fa-unlock]="!header.locked"></span>
                <span class="title"
                        [class.dragable]="header.dragable"
                        [textContent]="header.value"></span>
                <span class="icon fa" [class.hidden]="printMode || !items || items.length === 0" #icon
                        [class.fa-sort]="header.sortable"
                        [class.fa-sort-asc]="header.assending"
                        [class.fa-sort-desc]="header.desending"></span>
            </th>
            <th scope="col" *ngIf="action && !printMode" id="actionable" class="actionable"></th>
        </tr>
    </thead>
    <tbody>
        <tr *ngIf="!printMode && enableFiltering && items && items.length">
            <td scope="row" *ngIf="enableIndexing && !printMode" class="index filter">
                <input type="text" disabled style="opacity:0" />
            </td>
            <td scope="row" 
                        *ngFor="let header of headers; let i=index" 
                        [attr.data-label]="header.value" 
                        class="filter">
                <span *ngIf="header.filter === undefined">&nbsp;</span>
                <input  *ngIf="header.filter !== undefined"
                        id="filter-{{i}}"
                        type="text" 
                        (keyup)="changeFilter($event, header)"
                        [value]="header.filter ? header.filter : ''" />
                <label *ngIf="header.filter !== undefined" for="filter-{{i}}" ><span class="off-screen" >Filter "{{header.value}}"</span><span class="fa fa-search"></span></label>
            </td>
            <td scope="row" *ngIf="action && !printMode"></td>
        </tr>
       <ng-template ngFor let-item [ngForOf]="filteredItems" let-i="index">
            <tr (click)="actionClick($event, item)"
                (mouseover)="hover(item, true)"
                (mouseout)="hover(item, false)"
                [class.pointer]="action"
                [class.hover]="item.hover"
                [class.expanded]="item.expanded"
                [class.odd]="i%2">
                <td scope="row" class="index" *ngIf="enableIndexing && !printMode">
                    <span [textContent]="i + pageInfo.from + 1"></span></td>
                <td scope="row" 
                    *ngFor="let header of headers" 
                    [class]="toCssClass(header)"
                    [attr.data-label]="header.value" 
                    [intoName]="header.value"
                    [intoId]="header.key + '-' + i"
                    [into]="header.format"
                    [intoData]="item"
                    [rawContent]="cellContent(item, header)"
                    [onComponentChange]="onTableCellEdit.bind(this)"></td>
                <td scope="row" *ngIf="action && !printMode">
                    <a class="actionable"
                        *ngIf="expandable(item, true)"
                        tabindex="0"
                        role="button"
                        style="cursor:pointer"
                        [class.expanded]="item.expanded" #clicker
                        (keydown)="keydown($event, clicker)" (click)="actionClick($event, item)">
                        <span
                            class="icon fa"
                            [class.fa-angle-right]="!rowDetailer"
                            [class.fa-minus-square-o]="rowDetailer && item.expanded"
                            [class.fa-plus-square-o]="rowDetailer && !item.expanded"
                            aria-hidden="true"></span>
                        <span class="off-screen" [textContent]="offScreenMessage(item)"></span>
                    </a>
                </td>
            </tr>
            <tr *ngIf="rowDetailer && item.expanded" class="detail" [class.odd]="i%2">
                <td scope="row" class="index" *ngIf="enableIndexing && !printMode"></td>
                <td [attr.colspan]="columnsCount()">
                    <ng-container [ngTemplateOutlet]="rowDetailer" [ngTemplateOutletContext]="rowDetailerContext(item)"></ng-container>
                </td>
            </tr>
        </ng-template>
    </tbody>
</table>
`,
	styles: [`:host{display:inline-block!important;width:100%;position:relative;margin:0 auto;border-spacing:0;border-collapse:collapse}:host .off-screen{display:block;float:left;height:0;overflow:hidden;text-indent:-99999px;width:0}:host table{margin:1rem auto;padding:0;width:100%;table-layout:fixed;max-width:99%;background-color:transparent;border-collapse:collapse}:host table caption{background-color:#c3e5e2;border-radius:2px;color:#1b1b1b;caption-side:top;font-size:14px;padding:5px 6px;margin-bottom:15px;text-align:left}:host table thead{border-top:1px solid #bbb;border-bottom:1px solid #bbb;background-color:#eee}:host table tr{border:0}:host table tr.expanded td{font-weight:700}:host table td{padding-left:3px;min-height:23px}:host table td.index span{display:block;height:23px}:host table td:first-child{padding-left:5px}:host table td ::ng-deep input-component .locked,:host table td ::ng-deep input-component input{width:99%}:host table td .off-screen{display:block;float:left;height:0;overflow:hidden;text-indent:-99999px;width:0}:host table td.filter{padding:0;position:relative}:host table td.filter input{-webkit-box-sizing:border-box;box-sizing:border-box;width:100%;margin:0}:host table td.filter .fa{position:absolute;top:4px;right:2px;color:#bad}:host table td ::ng-deep img{height:24px}:host table td.index{background-color:#eee;border-right:1px solid #bbb}:host table th{cursor:default;-webkit-user-select:none;-moz-user-select:none;-o-user-select:none;-ms-user-select:none;user-select:none;height:24px;position:relative;white-space:nowrap;font-weight:400;text-transform:uppercase;font-size:14px;padding-top:6px;padding-bottom:6px;text-align:left}:host table th.drag-over{background-color:#9b9b9b}:host table th.drag-over .icon,:host table th.drag-over .title{color:#eee}:host table th:first-child{padding-left:5px}:host table th.ascending,:host table th.descending,:host table th.sortable{cursor:pointer;height:12px}:host table th.indexable{width:33px}:host table th.actionable{width:24px}:host table th .hidden{display:none}:host table th .title{color:#254a4d;display:inline-block;height:20px;white-space:nowrap}:host table th .dragable{cursor:move}:host table th .icon{width:22px;display:inline-block;height:20px;color:#254a4d}:host .fa.fa-angle-right{font-size:18px}table tr.expanded td{border-bottom:0}table tr.detail td{border-top:0;cursor:default}table tr.expanded td a.expanded{background-position:right 2px}table tbody tr.hover,table tbody tr:hover{background-color:#ffeed2}table tbody tr.detail.hover,table tbody tr.detail.hover td table thead tr,table tbody tr.detail:hover,table tbody tr.detail:hover td table thead tr{background-color:inherit}table tr td a.actionable{display:inline-table;height:32px;vertical-align:middle;width:25px;line-height:30px;color:#254a4d}table tbody tr.detail.hover td:last-child,table tbody tr.detail:hover td:last-child{border-right:0}table tbody tr.detail.hover td:first-child,table tbody tr.detail:hover td:first-child{border-left:0}table tr td{border-bottom:1px solid #b1b3b3;color:#254a5d;font-size:15px;text-transform:capitalize}table tbody tr.pointer{cursor:pointer}table.alert-danger{border:0}table.alert-danger caption{background-color:transparent;font-weight:700;margin-bottom:0}table.alert-danger td{border-bottom:0;display:block}table.alert-danger td:first-child{padding-left:0}table.alert-danger td:last-child{border-bottom:0}table.alert-danger td:before{content:attr(data-label);float:left;font-weight:700;text-transform:uppercase;width:20%}table.alert-danger td a span.icon{width:100%}table.alert-danger thead{border:none;clip:rect(0 0 0 0);height:1px;margin:-1px;overflow:hidden;padding:0;position:absolute;width:1px}table.alert-danger tr{border:2px solid #fff;display:block;margin-bottom:.625em;padding:5px;border-radius:5px}table.alert-danger tr th.actionable{width:inherit}table.alert-danger tr td{border-bottom:0}@media screen and (max-width:600px){table{border:0}table td{border-bottom:0;display:block;text-align:right}table td:first-child{padding-left:0}table td:last-child{border-bottom:0}table td.filter input{width:50%!important}table td.filter .fa{right:7px!important}table td:before{content:attr(data-label);float:left;font-weight:700;text-transform:uppercase}table td a span.icon{width:100%}table thead{border:none;clip:rect(0 0 0 0);height:1px;margin:-1px;overflow:hidden;padding:0;position:absolute;width:1px}table tr{border-bottom:3px solid #ddd;display:block;margin-bottom:.625em}table tr th.actionable{width:inherit}table tr td{border-bottom:0}table.alert-danger td:before{width:inherit}}`]
})
export class TableViewComponent implements OnInit, OnChanges {
	dragging = false;
	printMode = false;
	filteredItems = [];
	filteringTimerId: any;

    @Input("vocabulary")
    public vocabulary = {
		configureTable: "Configure Table",
		configureColumns: "Configure Columns",
		clickSort: "Click to Sort",
		setSize: "Set Size",
		firstPage: "First",
		lastPage: "Last",
		previousPage: "Previous"
	};

	@Input("lockable")
	lockable:boolean;

	@Input("caption")
    public caption: string;

    @Input("action")
    public action: string;

    @Input("pageInfo")
    public pageInfo;

    @Input("actionKeys")
    public actionKeys;

    @Input("tableClass")
    public tableClass = 'default-flexible-table';

	@Input("headers")
	public headers: any[];

	@Input("items")
	public items: any[];

	@Input("tableInfo")
	public tableInfo: any;

    @Input("enableIndexing")
    public enableIndexing: boolean;

    @Input("enableFiltering")
    public enableFiltering: boolean;

    @Input("rowDetailer")
    public rowDetailer: any;

    @Input("expandable")
    public expandable: any;

    @Input("expandIf")
    public expandIf: boolean;

    @Input("filterwhiletyping")
    public filterwhiletyping: boolean;

    @Input("rowDetailerHeaders")
    public rowDetailerHeaders: any;

	@Output('onaction')
	private onaction = new EventEmitter();

	@Output('onchange')
	private onchange = new EventEmitter();

	@Output('onfilter')
	private onfilter = new EventEmitter();

	@Output('onCellContentEdit')
	private onCellContentEdit = new EventEmitter();

	@ViewChild('flexible', {read: ViewContainerRef}) private table: ViewContainerRef;

    constructor(public el:ElementRef) {}


	private findColumnWithID(id: string) {
        const list = this.headerColumnElements();
		let column = null;
		for (let i = 0; i < list.length; i++) {
			if (list[i].getAttribute("id") === id) {
				column = list[i];
				break;
			}
		}
		return column;
	}

	private swapColumns(source: any, destination: any) {

		if (source.node.parentNode === destination.node.parentNode) {
			const srcIndex = this.getColumnIndex(source.medium.key);
			const desIndex = this.getColumnIndex(destination.medium.key);
			if (srcIndex < 0 || desIndex < 0) {
				console.log("invalid drop id", source.medium.key, destination.medium.key);
				return;
			}
			const x = this.filteredItems;
			this.filteredItems = [];

			setTimeout(()=>{
				const sobj = this.headers[srcIndex];
				this.headers[srcIndex] = this.headers[desIndex];
				this.headers[desIndex] = sobj;
				this.filteredItems = x;

				this.onfilter.emit(this.filteredItems);
				this.onchange.emit(this.headers);
			}, 33);
	
		} else if (source.medium.locked || destination.medium.locked) {
			const x = this.filteredItems;
			this.filteredItems = [];
			this.onfilter.emit(this.filteredItems);
			setTimeout(()=>{
				source.medium.locked = !source.medium.locked;
				destination.medium.locked = !destination.medium.locked;
				this.filteredItems = x;
				this.onfilter.emit(this.filteredItems);
				this.onchange.emit(this.headers);
			},33);
		}
	}

	private getColumnIndex(id: string) {
		let index = -1;
		for (let i = 0; i < this.headers.length; i++) {
			if (this.headers[i].key === id) {
				index = i;
				break;
			}
		}
		return index;
	}
	private itemValue(item, hpath) {
		let subitem = item;
		hpath.map( (subkey) => {
			if (subitem) {
				subitem = subitem[subkey];
			}
		})
		return subitem === undefined || subitem === null || subitem === "null" ? "" : String(subitem);
	}
	initVisibleRows() {
		const result = [];
		for (let i = 0; i < this.filteredItems.length; i++) {
			if (i >= this.pageInfo.from && i <= this.pageInfo.to) {
				result.push(this.filteredItems[i]);
			}
		}
		this.filteredItems = result;
	}

	lock(header: FlexibleTableHeader, event) {
        event.stopPropagation();	
        event.preventDefault();
		header.locked = !header.locked;
		this.onchange.emit(this.headers);
	}
	sort(header: FlexibleTableHeader, icon) {
		if (header.sortable && this.items && this.items.length) {
			for (let i = 0; i < this.headers.length ; i++) {
                const h = this.headers[i];

                if (h.key !== header.key) {
					const item = this.findColumnWithID(h.key);

					if (item) {
						item.classList.remove("ascending");
						item.classList.remove("descending");
						item.classList.add("sortable");
					}
                    h.descending = false;
                    h.ascending = false;
				}
			}
            icon.classList.remove("fa-sort");
			if (header.ascending || (!header.ascending && !header.descending)) {
				header.descending = true;
				header.ascending = false;
				icon.classList.remove("fa-sort-asc");
				icon.classList.add("fa-sort-desc");
			} else {
				header.descending = false;
				header.ascending = true;
				icon.classList.remove("fa-sort-desc");
				icon.classList.add("fa-sort-asc");
			}
			const hpath = header.key.split(".");

			if (this.enableFiltering) {
				this.filterItems();
			} else {
				this.filteredItems = this.items ? this.items : [];
			}
			this.filteredItems.sort((a, b) => {
				const v1 = this.itemValue(a, hpath);
				const v2 = this.itemValue(b, hpath);

				if (header.ascending) {
					return v1 > v2 ? 1 : -1;
				}
				return v1 < v2 ? 1 : -1;
			});
			this.initVisibleRows();
		}
	}

	offsetWidth() {
		return this.table.element.nativeElement.offsetWidth;
	}

	ngOnChanges(changes:any) {
		// if (changes.items) {
		// 	this.evaluateRows();
		// }
	}

	ngOnInit() {
		if (this.pageInfo) {
			if (!this.pageInfo.to) {
				this.pageInfo.to = this.pageInfo.pageSize;
			}
		} else {
			this.pageInfo = { 
                contentSize: 100000, 
                pageSize: 100000, 
                pages: 1, 
                from: 0, 
                to: 100000, 
                currentPage: 1, 
                maxWidth: "0" 
            };
		}
		if (!this.headers) {
			this.headers = [];
		}
		this.evaluateRows();
        if (this.actionKeys) {
            this.actionKeys = this.actionKeys.split(",");
		}
		if (!this.rowDetailer && this.expandable) {
			this.rowDetailer = function(item) {
				return {data: item, headers: []};
			};
		}
		if (!this.expandable) {
			this.expandable = function(item, showIcon) {return showIcon};
		}
		if (!this.rowDetailerHeaders) {
			this.rowDetailerHeaders = (item) => [];
		}
	}
	evaluateRows() {
		if (this.enableFiltering) {
			this.filterItems();
		} else {
			this.filteredItems = this.items ? this.items : [];
		}
		this.initVisibleRows();
	}

    headerColumnElements() {
		let result = [];

		if (this.table.element.nativeElement.children) {
			const list = this.table.element.nativeElement.children;
			result = this.caption ? list[1].children[0].children : list[0].children[0].children;
		}
		return result;
    }

	headerById(id) {
		let h;
		for (const i in this.headers) {
			if (this.headers[i].key === id) {
				h = this.headers[i];
				break;
			}
		}
		return h;
	}

    columnsCount() {
		let count = 0;
		this.headers.map( (item) => {
            if (item.present) {
                count++;
            }
		});
        if (this.action) {
            count++;
        }
        return count;
	}
	hover(item, flag) {
		if (flag) {
			item.hover = true;
		} else {
			delete item.hover;
		}
	}

	toCssClass(header) {
		return header.key.replace(/\./g,'-');
	}
    keydown(event, item) {
        const code = event.which;
        if ((code === 13) || (code === 32)) {
			item.click();
		}
    }
    offScreenMessage(item) {
		let message: string = this.action;
		if (this.actionKeys) {
			this.actionKeys.map((key) => { message = message.replace(key, item[key.substring(1, key.length - 1)]); })
		}
        return message;
    }

    cellContent(item, header) {
		let content = this.itemValue(item, header.key.split("."));
        return (content !== undefined && content != null && content.length) ? content : '&nbsp;';
	}

	rowDetailerContext(item) {
		return {
			data: item,
			tableInfo: this.tableInfo,
			headers: this.rowDetailerHeaders(item)
		};
	}

	changeFilter(event, header) {
        const code = event.which;

		header.filter = event.target.value;

		if (this.filterwhiletyping || code === 13) {
			if(this.filteringTimerId) {
				clearTimeout(this.filteringTimerId);
			}
			this.filteringTimerId = setTimeout(()=>{
				this.filterItems();
				this.initVisibleRows();
				this.filteringTimerId  = undefined;
			}, 123);
		}
	}
	actionClick(event, item: any) {
		event.stopPropagation();
        if (this.rowDetailer && (this.expandIf || this.expandable(item, false)) ) {
            if (item.expanded) {
                delete item.expanded;
            } else {
                item.expanded = true;
            }
        } else {
            this.onaction.emit(item);
		}
		return false;
	}

	print() {
		this.printMode = true;
		setTimeout(()=>{
			const content = this.el.nativeElement.innerHTML;
			this.printMode = false;
			const popupWin = window.open('', '_blank', 'width=300,height=300');
		
			popupWin.document.open();
        	popupWin.document.write('<html><body onload="window.print()">' + content + '</html>');
        	popupWin.document.close();
		},3);
	}

	// <5, !5, >5, *E, E*, *E*
	private shouldSkipItem(value, filterBy) {
		let result = false;

		if (value !== undefined && value !== null && value.length) {			
			if (filterBy[0] === '<') {
				result = filterBy.length > 1 && parseFloat(value) >= parseFloat(filterBy.substring(1));
			} else if (filterBy[0] === '>') {
				result = filterBy.length > 1 && parseFloat(value) <= parseFloat(filterBy.substring(1));
			} else if (filterBy[0] === '!') {
				result = filterBy.length > 1 && parseFloat(value) == parseFloat(filterBy.substring(1));
			} else if (filterBy[0] === '=') {
				result = filterBy.length == 1 || parseFloat(value) !== parseFloat(filterBy.substring(1));
			} else if (filterBy[0] === '*' && filterBy[filterBy.length-1] !== '*') {
				const f = filterBy.substring(1);
				result = value.indexOf(f) !== value.length - f.length
			} else if (filterBy[0] !== '*' && filterBy[filterBy.length-1] === '*') {
				const f = filterBy.substring(0, filterBy.length-1);
				result = value.indexOf(f) !== 0;
			} else if (filterBy[0] === '*' && filterBy[filterBy.length-1] === '*') {
				result = filterBy.length > 1 && value.indexOf( filterBy.substring(1, filterBy.length-1) ) < 0;
			} else {
				result = value.indexOf(filterBy) < 0;
			}
		}
		return result;
	}
	filterItems() {
		this.filteredItems = this.items ? this.items.filter((item) => {
			let keepItem = true;

			for (let i = 0; i < this.headers.length; i++) {
				const header = this.headers[i];
				if (header.filter && header.filter.length) {
					const v = this.itemValue(item, header.key.split("."));

					if (this.shouldSkipItem(v,header.filter)) {
						keepItem = false;
						break;
					}
				}
			}
			return keepItem;
		}) : [];
		this.onfilter.emit(this.filteredItems);
	}

	onTableCellEdit(event) {
		const id = event.id.split("-");
		const n = event.name;
		const v= event.value;
		const t = this.items[parseInt(id[1])];

		if (t) {
			const list = id[0].split(".");
			let subitem = t[list[0]];
			for(let i = 1; i < (list.length - 1); i++) {
				subitem = subitem[list[i]]
			}
			if (subitem && list.length > 1){
				subitem[list[list.length - 1]] = v;
			}
			this.onCellContentEdit.emit({name: n, value: v, item: t});
		}
    }

	dragEnabled(event: DragEvent) {
		return event.medium.dragable;
	}
	dropEnabled(event: DropEvent) {
		return event.destination.medium.dragable;
	}
	onDragStart(event: DragEvent){
//        this.dragging = true;
	}
	onDragEnd(event: DragEvent){
 //       this.dragging = false;
	}
	onDrop(event:DropEvent){
		this.swapColumns(event.source, event.destination);
	}
}
